@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
body {
  margin: 0;
  font-family: 'Nanum Gothic', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
.App {
   background-color: #232840;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   width: 100%;
   height: 100vh;
   color: #d5d5d5;
}

.hbox {
   display: flex;
}

.vbox {
   display: flex;
   flex-direction: column;
}

#author a {
   text-decoration: none;
   color: #F89D98;
}

.Welcome, .Weather {
   background-color: #131521;
   width: 360px;
   height: 350px;
   padding: 50px;
   align-items: center;
}

.bounce {
   -webkit-animation-name: bounce;
           animation-name: bounce;
   -webkit-animation-timing-function: linear;
           animation-timing-function: linear;
}

@-webkit-keyframes bounce {
   0%   { transform: scale(1,1) translateY(0); }
   10%   { transform: scale(1,.9) translateY(0); }
   30%  { transform: scale(1,1) translateY(-15%); }
   50%  { transform: scale(1,1) translateY(0); }
   100% { transform: scale(1,1) translateY(0); }
}

@keyframes bounce {
   0%   { transform: scale(1,1) translateY(0); }
   10%   { transform: scale(1,.9) translateY(0); }
   30%  { transform: scale(1,1) translateY(-15%); }
   50%  { transform: scale(1,1) translateY(0); }
   100% { transform: scale(1,1) translateY(0); }
}

.blow-slow {
   -webkit-animation-name: blow-slow;
           animation-name: blow-slow;
   -webkit-animation-timing-function: linear;
           animation-timing-function: linear;
}

@-webkit-keyframes blow-slow {
   0%   { transform: scale(1,1) translateY(0); }
   50%  { transform: scale(.9,.9) translateY(0); }
   100% { transform: scale(1,1) translateY(0); }
}

@keyframes blow-slow {
   0%   { transform: scale(1,1) translateY(0); }
   50%  { transform: scale(.9,.9) translateY(0); }
   100% { transform: scale(1,1) translateY(0); }
}

